import React, { Component } from "react";

export class Topic extends Component {
  render() {
    return (
      <div className="About-topic">
        <h1 className="about-topic-style-1">About</h1>
        <h3 className="about-topic-style">
          Good News is a church where anyone can be transformed by the story of
          Jesus. We are a multicultural Christian congregation that meets in the
          Aksarben area.
        </h3>
      </div>
    );
  }
}

export default Topic;
