import React, { Component } from "react";

export class Project extends Component {
  render() {
    return (
      <div>
        <div>
          <img
            className="Project-header-pic"
            height="700"
            width="700"
            alt="Project-page-pic"
            src="Project-pic.jpg"
          ></img>
          <h2 className="Project-icon">Explore</h2>
          <h2 className="Project-icon-2">
            Though new thing we learn and we grow
          </h2>
        </div>
        <h1 className="Project-page-main-header-title"> volunteer Project</h1>

        <div className="Project-page-volunteer">
          <div>
            <img
              className="Project-page-volunteer-pic"
              height="600"
              width="100%"
              alt="volunteer-project-pic"
              src="volunteer-pic.jpg"
            ></img>
          </div>
          <div>
            <p className="volunteer-page-paragraph">
              During the trip to North Indian We helped local school called
              S.g.r Public School located in 248001, Kandholi, Chironwali,
              Dehradun, Uttarakhand 248001, India bring about change to their
              school system. At that time, we worked as team alongside local
              staff and provided hands-on support within local community
              centers, schools and kindergartens.
            </p>
            <p className="volunteer-page-paragraph">
              At that school the age of the children ranges from infants all the
              way to 12 years old. During the time when we were helping others
              our role change from day to day from invlove organizing games,
              arts and crafts, singing, playing sports, as well as teaching
              manners and colors to the children We had lot of fun and evey one
              gain valuable insight through our time helping others. We stayed
              at that school for about one month before turning home in the
              summer season.
            </p>
          </div>
        </div>
        <div className="project-middle">
          <h2 className="project-middle-header">SummerFest 2019</h2>
          <iframe
            className="project-page-video"
            title="youtube-background-video"
            width="850"
            height="600"
            src="https://www.youtube.com/embed/wP9AnsKtrmo"
            frameBorder="0"
            allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
          <p className="project-end-paragraph">
            During the summer of last year trip our church group went to see
            Chris Tomlin performing Lay Me Down in Arigion United states.
          </p>
          <p className="project-end-paragraph">
            We gain so much insight as a group when we attented the festial.
          </p>
        </div>
      </div>
    );
  }
}

export default Project;
